import {Component, ViewChild, ChangeDetectorRef  } from '@angular/core';
import {NavController, ToastController} from 'ionic-angular';
import {AlertController} from 'ionic-angular';
import {Content } from 'ionic-angular';
import { Events } from 'ionic-angular';

import {MyHttp} from '../../util/MyHttp';
import {Memory} from '../../util/Memory'
import {ImgService} from '../../util/ImgService'
import {CalculateService} from '../../util/CalculateService'

import {HomeIntroducePage} from '../../pages/home-introduce/home-introduce'
import {StartPage} from "../start/start";
import {UserDetailPage} from "../user-detail/user-detail"
import {CredListPage} from "../credList/credList";
import {PayPage} from "../purchase/pay";
import {SearchPage} from "../search/search";
import {CommunicatePage} from "../communicate/communicate";

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})

export class HomePage {
  @ViewChild(Content) content: Content;//Content

  //


  /**
   * 
   */
  public userList = [];
  public id;
  public pageIndex;

  public displayTitle = true;

  public searchInfo =  {
    high : '',
    age : '',
    income : '',
    edu : ''
  };

  //
  public isSearch;

  // @ViewChild("homeSlides") slides: Slides;

  constructor(public navCtrl:NavController, private myHttp:MyHttp, public alertCtrl:AlertController,
              public memory:Memory, public imgService:ImgService, public events: Events,
              public calculateService: CalculateService, private cdr: ChangeDetectorRef,
              public toastCtrl: ToastController) {
    this.pageIndex = 1;
    this.getUserList();
    this.events.subscribe('e-home-list', () => {
      this.pageIndex = 1;
      this.userList = [];
      this.getUserList();
    });
    this.events.subscribe('e-home-search', (searchInfo,isSearch)=>{
      this.pageIndex = 1;
      this.searchInfo = searchInfo;
      this.isSearch = isSearch;
      this.userList = [];
      this.getUserList();
    })
  }

  ngAfterViewInit() {
    this.content.ionScroll.subscribe(event => {
      // console.log(event)
      if (event.directionY == "down") {
        this.displayTitle = false;
        document.getElementById("head").style.opacity ="0";
      } else {
        this.displayTitle = true;
        document.getElementById("head").style.opacity ="1";
      }
    });
  }

  doRefresh(refresher) {
    if(this.isSearch){
      //
      this.searchInfo = {
        high : '',
        age : '',
        income : '',
        edu : ''
      };
    }
    this.pageIndex = 1;
    this.userList = [];
    this.getUserList();
    setTimeout(() => {
      refresher.complete();
    },2000);
  }

  /**
   * VIP
   */
  nextPage() {
    if(!this.isLogin()){
      //
      this.goToLogin();
    }else{
      //
      if(this.isLoginOnce()){
        //
        this.goToDetail();
      }else{
        //
        if(this.isCredMain()){
          //
          this.pageIndex ++;
          this.getUserList();
/*          if (this.calculateService.isVip(this.memory.getUser().vipTime)) {
            //VIP
          }else{
            //VIP
            this.goToBuyVip()
          }*/
        }else{
          //
          this.goToCred();
        }
      }
    }
  }

  /**
   * 
   */
  getUserList() {
    if (this.isLogin()) {
      this.id = this.memory.getUser().id;
      console.log(this.id+"id")
    }else{
      this.id = this.memory.getSex();
      console.log(this.id+"id")
    }
    this.searchInfo['userId'] = this.id;
    this.searchInfo['size'] = 5;
    this.searchInfo['index'] = this.pageIndex;
    if (this.id) {
      this.myHttp.post(MyHttp.URL_USER_SCREEN_LIST, this.searchInfo, (data) => {
        console.log(data)
        if (data.listResult === '0') {
          if(!this.isLoginOnce()){
            this.userList = this.userList.concat(data.userList);
          }else{
            this.goToDetail();
          }
        } else if (data.listResult === '1'){
          this.alertCtrl.create({
            message: '',
            buttons: [{
              text: 'OK',
              handler: ()=> {
              }
            }]
          }).present();
        } else if (data.listResult === '2'){

        }

        //
        let isMsg = this.memory.getMsg();
        if(isMsg){
          this.searchPub("~");
        }

      })
    }

  }

  /**
   *
   * @param userId
   */
  getIntroduce(userId:any) {
    if (!this.isLogin()) {
      //
      this.goToLogin();
    } else if (!this.isCredMain()) {
      //
      this.goToCred();
    } else {
      this.navCtrl.push(HomeIntroducePage, {otherUserId: userId})
    }
  }


  /**
   * Listuser
   * @param user
   */
  deleteFromList(event,id){
    this.userList.splice(id,1);
    event.stopPropagation();
  }



  /**
   * 
   * @returns {boolean}
     */
  isCredMain() {
    return this.memory.getUser().mainCredNum >= 3;
  }

  /**
   * 
   */
  isLoginOnce(){
    let user = this.memory.getUser();
    //
    if(this.isLogin()){
      if(user.nickName === ''||user.name===''||(user.sex !==0 && user.sex !==1)||user.birthday===''){
        return true;
      }
    }
    return false;
  }

  /**
   * 
   */
  goToDetail(){
    this.alertCtrl.create({
      message: '~()',
      buttons: [{
        text: 'OK',
        handler: ()=> {
          this.navCtrl.push(UserDetailPage);
        }
      }]
    }).present();
  }

  /**
   * Vip
   */
  goToBuyVip(){
    this.alertCtrl.create({
      message: '~VIP',
      buttons: [{
        text:'NO'
      },
        {
          text: 'OK',
          handler: ()=> {
            this.navCtrl.push(PayPage);
          }
        }

      ]
    }).present();
  }

  /**
   * 
   */
  goToCred() {
    this.alertCtrl.create({
      message: '~',
      buttons: [
        {
          text: 'NO'
        },
        {
          text: 'OK',
          handler: ()=> {
            this.navCtrl.push(CredListPage);
          }
        }
      ]
    }).present();
  }

  /**
   * 
   */
  goToLogin() {
    this.alertCtrl.create({
      message: '~',
      buttons: [{
        text: 'OK',
        handler: ()=> {
          this.memory.setSex({});
          this.navCtrl.setRoot(StartPage);
        }
      }]
    }).present();
  }

  /**
   * 
   */
  goToSearch() {
    if (!this.isLogin()) {
      this.goToLogin()
    } else if(!this.isCredMain()){
      this.goToCred();
    }else{
      this.navCtrl.push(SearchPage);
    }
    /*this.searchPub("");*/
  }

  /**
   * 
   * @returns {any}
     */
  isLogin() {
    return this.memory.getUser().id ? true : false;
  }


  /**
   * 
   * @param subTitle
   */
  searchPub(subTitle: string) {
    this.alertCtrl.create({
      title: "",
      subTitle: subTitle,
      buttons: [""]
    }).present();
  }

  /**
   * 
   * @param user
     */
  like(event, index) {
    this.cdr.detach();
    this.changeRelation(0, this.userList[index].id, (data)=> {
      if (data.likeResult == "0") {
        this.userList[index].relation = 1;
        this.cdr.reattach();
      }
    })
    event.stopPropagation();
  }

  /**
   * 
   * @param user
   */
  disLike(event, index) {
    this.cdr.detach();
    this.changeRelation(2, this.userList[index].id, (data)=> {
      if (data.disLikeResult == "0") {
        this.userList[index].relation = 0;
        this.cdr.reattach();
      }
    })
    event.stopPropagation();

  }

  /**
   * 
   * @param user
     */
  hate(event, index) {
    this.changeRelation(1, this.userList[index].id, (data)=> {
      if (data.hateResult == "0") {
        for (index; index < this.userList.length-1; index++) {
          this.userList[index] = this.userList[index+1];
        }
        this.userList.pop();
      }
    })
    event.stopPropagation();
  }

  /**
   *
   * @param type 012345
   */
  changeRelation(type: number, toUserId, successBack:Function) {
    let url = [MyHttp.URL_LIKE,MyHttp.URL_HATE,MyHttp.URL_DIS_LIKE,MyHttp.URL_DIS_HATE,MyHttp.URL_COLLECT,MyHttp.URL_DIS_COLLECT][type];
    this.myHttp.post(url, {
      userId: this.memory.getUser().id,
      toUserId: toUserId
    }, (data) => {
      //
      if(type===0){
        this.memory.setLike(true);
      }
      successBack(data);
    })
  }

  goToMessage(event, user) {
    if(this.calculateService.isVip(this.memory.getUser().vipTime)){
      this.navCtrl.push(CommunicatePage,{
        person:user.id
      });
    }else{
      //vip,
      let prompt = "~Vip";
      this.alertCtrl.create({
        message: prompt,
        buttons: [
          {
            text:''
          },
          {
            text:'',
            handler:()=>{
              //vip
              this.navCtrl.push(PayPage);
            }
          }

        ]
      }).present();
    }
    event.stopPropagation();
  }
}



