  /**
   * 
   * @param {string} subTitle
   */
  backMessage() {
    let prompt = "";
    this.alertCtrl.create({
      subTitle: prompt,
      buttons: [
        {
          text:'',
          handler:()=>{
            clearInterval(this.timer);
            this.changeDetectorRef.detach();
            this.navCtrl.pop();
          }
        },
        {
          text:''
        }
      ]
    }).present();
  }


  //password
